import Papa from 'papaparse'
import { SmtComponent } from '../../../interfaces/SmtComponent'
import { CSV_VALID_COLS } from '../../../constants/Constants'
import { useDispatch } from 'react-redux'
import { setAssignments } from '../../../state/actions'
import FileUploadButton from '../file/FileUploadButton'
import { Assignment } from '../../../interfaces/Assignment'

interface CsvFileUploaderProps {
  className: string
  title: string
  fileFormat: string
}

const CsvFileUploader = ({
  className,
  title,
  fileFormat,
}: CsvFileUploaderProps) => {
  const dispatch = useDispatch()

  const fileHandler = (files: FileList | null) => {
    if (files) {
      Papa.parse(files[0], {
        header: true,
        skipEmptyLines: true,
        complete: function (results) {
          const cols = [...Object.keys(results.data[0] as string[])]

          if (CSV_VALID_COLS.every((r) => cols.includes(r))) {
            const smtComponents: SmtComponent[] = results.data.map(
              (element: any) => {
                return {
                  id: element['Part ID'],
                  value: element['Value'],
                  package: element['Package'],
                  rotation: Number(element['Rotation']),
                  x: Number(element['X']),
                  y: Number(element['Y']),
                } as SmtComponent
              }
            )

            smtComponents.sort((a, b) =>
              a.id < b.id ? -1 : a.id > b.id ? 1 : 0
            )

            let assignments: Assignment[] = []

            // const valuePackageExists = (
            //   assignments: Assignment[],
            //   valueToCheck: string,
            //   packageToCheck: string
            // ) => {
            //   let assignmentFound = undefined
            //   assignments.forEach((assignment) => {
            //     if (
            //       assignment.components.find(
            //         (component) =>
            //           component.value === valueToCheck &&
            //           component.package === packageToCheck
            //       )
            //     ) {
            //       assignmentFound = assignment
            //     }
            //   })

            //   return assignmentFound
            // }

            let lastIndexAssignment = 0

            smtComponents.forEach((componentCsv) => {
              const indexAssignment = assignments.findIndex(({ components }) =>
                components.find(
                  (component) =>
                    component.value === componentCsv.value &&
                    component.package === componentCsv.package
                )
              )

              if (indexAssignment != -1) {
                assignments[indexAssignment].components.push(componentCsv)
              } else {
                assignments.push({
                  id: lastIndexAssignment,
                  components: [componentCsv],
                  railId: undefined,
                })
                lastIndexAssignment++
              }
            })

            setAssignments(dispatch, assignments)
          } else {
            //File non valido
            alert('File CSV non valido!')
          }
        },
      })
    }
  }

  return (
    <FileUploadButton
      className={className}
      title={title}
      handleFileCallback={fileHandler}
      fileFormat={fileFormat}
    />
  )
}

export default CsvFileUploader
