import { Table } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { MAIN_TABLE_COLS } from '../../../constants/Constants'
import { Assignment } from '../../../interfaces/Assignment'
import { RootState } from '../../../state/store'
import AssignmentRow from './AssignmentRow'

const MainTable = () => {
  const tableData = useSelector<RootState, Assignment[]>(
    (state: RootState) => state.smtAppReducer.assignments
  )

  return (
    <Table bordered striped>
      <thead>
        <tr className='align-middle text-center'>
          {MAIN_TABLE_COLS.map((rows, index) => {
            return <th key={index}>{rows}</th>
          })}
        </tr>
      </thead>
      <tbody>
        {
          //Colonne dei componenti
          tableData?.map((row, index) => {
            return <AssignmentRow key={index} assignmentData={row} />
          })
        }
      </tbody>
    </Table>
  )
}

export default MainTable
