import { Assignment } from '../../../interfaces/Assignment'
import RailSelect from './RailSelect'

interface AssignmentRowProps {
  assignmentData: Assignment
}

const AssignmentRow = ({ assignmentData }: AssignmentRowProps) => {
  return (
    <tr className='align-middle text-center'>
      <td>
        <ul className='list-unstyled'>
          {assignmentData.components.map(({ id }, index) => {
            return <li key={index}>{id}</li>
          })}
        </ul>
      </td>
      <td>{assignmentData.components[0]?.value}</td>
      <td>{assignmentData.components[0]?.package}</td>
      <td>
        <ul className='list-unstyled'>
          {assignmentData.components.map(({ rotation }, index) => {
            return <li key={index}>{rotation}</li>
          })}
        </ul>
      </td>
      <td>
        <ul className='list-unstyled'>
          {assignmentData.components.map(({ x }, index) => {
            return <li key={index}>{x}</li>
          })}
        </ul>
      </td>
      <td>
        <ul className='list-unstyled'>
          {assignmentData.components.map(({ y }, index) => {
            return <li key={index}>{y}</li>
          })}
        </ul>
      </td>
      <td>
        <RailSelect
          idAssignment={assignmentData.id}
          selectedIdRail={assignmentData.railId}
        />
      </td>
    </tr>
  )
}

export default AssignmentRow
